.about-container {
  display: flex; /* Flexbox를 사용하여 자식 요소들을 나란히 배치 */
  align-items: center; /* 세로 방향으로 상단 정렬 */
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: black;
  border: 2px solid white;
  color: white;
  font-family: "Fira Mono", monospace;
  width: 100%;
  box-sizing: border-box; /* 패딩 포함하여 너비 계산 */
  padding: 10px; /* 패딩 추가 */
}

.text-container {
  text-align: left;
  flex-direction: column; /* 세로 방향으로 정렬 */
}

.about-image {
  width: 8vw; /* 이미지 크기를 화면 너비의 일정 비율로 조정 */
  height: auto; /* 이미지의 비율 유지 */
  margin-right: 24px; /* 텍스트와의 간격 조절 */
}

.about-name {
  font-size: 1.5vw; /* 반응형 폰트 크기 */
  font-weight: 700; /* Semi bold for the text */
  margin-bottom: 0;
}

.about-sea {
  font-family: "Sam3KRFont";
  font-size: 1.2vw; /* 반응형 폰트 크기 */
  margin-top: 4px;
}

.about-profession {
  font-weight: 400; /* Semi bold for the text */
  margin-top: 5px; /* Reduced top margin to bring it closer to about-name */
  font-size: 1vw; /* 반응형 폰트 크기 */
}

.about-description {
  font-weight: 400; /* Semi bold for the text */
  margin: 15px 0; /* Increased margin to make it wider */
  font-size: 1vw; /* 반응형 폰트 크기 */
}

/* 반응형 디자인을 위해 모바일 사이즈에서 폰트 크기 조정 */
@media (max-width: 560px) {
  .about-container {
    flex-direction: column; /* 모바일에서는 세로 방향으로 정렬 */
    align-items: flex-start; /* 텍스트를 왼쪽으로 정렬 */
    width: 100%; /* Full width on mobile-sized screens */
  }

  .about-image {
    width: 16vw; /* 모바일에서 이미지 크기 줄이기 */
    margin: 10px 0; /* 텍스트와의 간격 조절 */
  }

  .about-name {
    font-size: 4vw; /* 모바일에서 폰트 크기 조정 */
  }

  .about-sea {
    font-size: 3vw; /* 모바일에서 폰트 크기 조정 */
  }

  .about-profession {
    font-size: 2.5vw; /* 모바일에서 폰트 크기 조정 */
  }

  .about-description {
    font-size: 2.5vw; /* 모바일에서 폰트 크기 조정 */
  }
}
