.works-item {
  background-color: black;
  color: white;
  display: flex;
  flex-direction: column;
  padding: 10px;
  position: relative;
}

.works-link {
  text-decoration: none;
  color: inherit;
}

.works-image-container {
  width: 100%;
  height: 0;
  padding-bottom: 120%; /* 4:3 비율 유지 */
  position: relative;
  overflow: hidden;
  margin-bottom: 16px;
}

.works-image {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
  transition: filter 0.3s ease, transform 0.3s ease;
}

.works-image-container:hover .works-image {
  filter: blur(4px) brightness(0.7);
  transform: scale(1.05);
}

.corner-rect {
  position: absolute;
  top: 10px;
  left: 10px;
  width: 20px;
  height: 20px;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.works-image-container:hover .overlay {
  opacity: 1;
}

.works-title-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: calc(0.75vw + 0.75vh + 1vmin);
  font-weight: bold;
  color: white;
  text-align: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.works-image-container:hover .works-title-overlay {
  opacity: 1;
}

.works-title {
  font-size: calc(0.75vw + 0.75vh + 1vmin);
  font-weight: 700;
  margin-right: 10px;
  color: white;
  text-align: left;
  margin-top: 8px;
}

.works-hashtags-container {
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
}

.works-hashtag {
  font-size: calc(0.75vw + 0.75vh + 0.75vmin);
  border: 1px solid #fff;
  border-radius: 100px;
  padding: 3px 12px;
  display: inline-block;
}

@media (max-width: 768px) {
  .works-title-overlay {
    font-size: calc(1.5vw + 1.5vh + 1.5vmin); /* 모바일 장치에서 폰트 크기 조정 */
  }
  
  .works-title {
    font-size: calc(1.5vw + 1.5vh + 1.5vmin); /* 모바일 장치에서 폰트 크기 조정 */
  }
  
  .works-hashtag {
    font-size: calc(1vw + 1vh + 1vmin); /* 모바일 장치에서 폰트 크기 조정 */
  }
}
