.App {
  text-align: center;
  background-color: black;
  color: white;
  font-family: "Fira Mono", monospace;
  overflow: hidden;

  mix-blend-mode: difference;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  animation: fadeIn 2s ease-in-out;
}

.main-title {
  font-size: 14vw; /* Adjust the font size to fit the screen width */
  margin-top: 20vh; /* Position the title towards the top */
  font-family: "EB Garamond", serif;
  transform: scaleX(0.7);
  font-weight: 500;

  font-style: italic;

  transform: scaleX(0.7);
  letter-spacing: -4px; /* 자간을 좁힘 */
}

/* Hide scrollbar for Chrome, Safari and Opera */
html::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
html {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

@font-face {
  font-family: "Sam3KRFont";
  src: url("https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_eight@1.0/Sam3KRFont.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
