.works-detail-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: black;
  color: white;
  margin-top: 16vw;
}

.corner-rect_detail {
  position: relative; /* 상대적 위치 설정 */
  z-index: 10; /* 해더보다 높은 z-index 값을 설정 */
  width: 4vw;
  height: 4vw;
  margin-top: -8vw; /* 해더 아래로 위치 조정 */
}

.works-detail-image {
  width: 250px;
  height: 250px;
  object-fit: cover;
  margin-bottom: 20px;
}

.works-detail-content {
  width: 90%; /* 콘텐츠 너비를 화면 너비의 90%로 설정 */
}

.works-detail-title {
  margin-bottom: 10px;
  font-family: Helvetica, sans-serif;
  font-size: calc(
    1.5vw + 0.5vh + 0.5vmin
  ); /* 폰트 크기를 화면 크기에 비례하여 조절 */
  margin-bottom: 10px;
  text-align: left;
}

.works-detail-date {
  font-family: Helvetica, sans-serif;
  font-size: calc(
    1vw + 0.5vh + 0.5vmin
  ); /* 폰트 크기를 화면 크기에 비례하여 조절 */
  margin-bottom: 20px;
  text-align: left;
}

.markdown-body {
  text-align: left;
  font-family: Helvetica, sans-serif;
  color: white;
  line-height: 1.6;
  font-size: calc(
    1vw + 0.5vh + 0.5vmin
  ); /* 폰트 크기를 화면 크기에 비례하여 조절 */
}

.markdown-body h2 {
  margin-top: 20px;
  margin-bottom: 10px;
  font-size: calc(
    1.2vw + 0.5vh + 0.5vmin
  ); /* 폰트 크기를 화면 크기에 비례하여 조절 */
  text-align: left;
}

.markdown-body p {
  margin-bottom: 10px;
  text-align: left;
}

.markdown-body img {
  width: auto; /* 너비는 자동으로 조절됩니다. */
  max-width: 100%; /* 최대 너비는 부모 요소에 맞게 설정됩니다. */
  height: 800px; /* 높이는 자동으로 조절됩니다. */
  object-fit: cover; /* 이미지가 요소에 맞게 자동으로 맞추어집니다. */
  margin-bottom: 20px;
}

.markdown-body ul {
  list-style: disc;
  margin-left: 20px;
  text-align: left;
}

.navigation-links {
  height: 24vw;
  display: flex;
  justify-content: space-between;
  align-items: center; /* 세로 중앙 정렬 */
  flex-direction: row; /* 가로 방향으로 정렬 */
  margin-top: 50px; /* 상단 여백 추가 */
  border-top: 1px solid white; /* 상단에 흰색 테두리 추가 */
}

.prev-link {
  text-align: left;
  font-size: 2vw;
  font-weight: 700;
  padding: 10px;
  color: #fff; /* 글자색 설정 */
  text-decoration: none; /* 밑줄 제거 */
  font-family: Helvetica, sans-serif; /* Helvetica 폰트 적용 */
  transition: background-color 0.3s; /* 배경색 변경에 대한 부드러운 전환 효과 */
}

.next-link {
  text-align: right;
  font-size: 2vw;
  font-weight: 700;
  padding: 10px;
  color: #fff; /* 글자색 설정 */
  text-decoration: none; /* 밑줄 제거 */
  font-family: Helvetica, sans-serif; /* Helvetica 폰트 적용 */
  transition: background-color 0.3s; /* 배경색 변경에 대한 부드러운 전환 효과 */
}

.prev-link:hover,
.next-link:hover {
  background-color: #2a2a2a; /* 호버 시 배경색 변경 */
}

/* 화면 너비가 큰 경우 폰트와 이미지 크기 조정 */
@media (min-width: 1200px) {
  .works-detail-title {
    font-size: calc(1vw + 0.5vh + 0.5vmin); /* 더 작은 폰트 크기로 조절 */
  }

  .works-detail-date {
    font-size: calc(0.75vw + 0.25vh + 0.25vmin); /* 더 작은 폰트 크기로 조절 */
  }

  .markdown-body {
    font-size: calc(0.75vw + 0.25vh + 0.25vmin); /* 더 작은 폰트 크기로 조절 */
  }

  .markdown-body h2 {
    font-size: calc(1vw + 0.5vh + 0.5vmin); /* 더 작은 폰트 크기로 조절 */
  }

  .markdown-body img {
    height: 600px; /* 더 작은 이미지 높이로 조절 */
  }
}

/* 화면 너비가 좁은 경우 (모바일) */
@media (max-width: 767px) {
    .works-detail-container {
      padding: 10px;
      margin-top: 20vw;
    }
  
    .works-detail-title {
      font-size: 4vw;
      text-align: left;
    }
  
    .works-detail-date {
      font-size: 3vw;
      text-align: left;
    }
  
    .works-detail-content {
      width: 95%;
    }
  
    .works-detail-image {
      width: 80vw;
      height: auto;
      margin-bottom: 10px;
    }
  
    .markdown-body {
      font-size: 3.5vw;
      line-height: 1.4;
    }
  
    .markdown-body h2 {
      font-size: 4vw;
      text-align: left;
    }
  
    .markdown-body img {
      height: auto;
      margin-bottom: 10px;
    }
  
    .markdown-body ul {
      margin-left: 15px;
    }
  
    .navigation-links {
      flex-direction: column;
      height: auto;
      margin-top: 30px;
      border-top: none;
    }
  
    .prev-link, .next-link {
      font-size: 4vw;
      text-align: center;
      width: 100%;
      padding: 15px;
      margin-bottom: 10px;
    }
  }