/* Snow.css */
.square {
  width: 16px;
  height: 16px;
  position: absolute;
  animation-name: fall;
  animation-timing-function: linear;
  animation-iteration-count: infinite;

  transition: transform 0.2s ease-out;

  box-shadow: 0px 0px 5px 2px rgba(255, 255, 255, 0.5); /* White shadow for visibility */
}

.clickable {
  width: 16px;
  height: 16px;
  animation: fall 10s linear infinite;
}

.clickable:hover {
  cursor: pointer;
  animation-play-state: paused;
  width: 20px;
  height: 20px;
}

.unclickable {
  transform: translate(0, 0);
  mix-blend-mode: difference;
}
.unclickable.move {
  top: 20px; /* 마우스 커서 주변에서 20px 아래로 이동 */
  transition: top 0.3s ease; /* 부드러운 이동을 위한 트랜지션 추가 */
}

@keyframes fall {
  0% {
    top: -20px; /* 사각형이 항상 상단에서 시작하도록 설정 */
  }
  100% {
    top: calc(100vh + 20px); /* 화면 높이만큼 이동 */
  }
}
