/* Bubble.css */
.bubble {
  display: none; /* Initially hidden */
  position: absolute;
  padding: 10px;
  background-color: black;
  border: 2px solid white;
  color: white;
  font-family: "Helvetica", sans-serif;
  font-weight: normal;
  white-space: nowrap; /* Prevents text wrapping */
  z-index: 20; /* Ensures it's above other elements */
  transform: translateX(10%);
}

.bubble.visible {
  display: block; /* Show the bubble when visible */
}

.bubble-content {
  display: flex;
  align-items: center;
}

.bubble-thumbnail {
  width: 100px; /* Set desired thumbnail size */
  height: 100px;
  overflow: hidden; /* Ensure the image is contained within the div */
  margin-right: 10px;
  background-color: grey; /* Placeholder color */
}

.bubble-thumbnail img {
  width: 100%;
  height: 100%;
  object-fit: cover; /* Ensure the image is cropped without distortion */
}

.bubble-info {
  text-align: left;
}

.bubble-title,
.bubble-date,
.bubble-description {
  margin: 2px 0;
}
